/* ===============================
 * Red badge
 * ============================ */
 .badges_red {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 1px 5px;
    background: #FF4E00;
    border-radius: 100px;
    cursor: pointer;
    transition: 100ms;
    margin: 8px;
}
.badges_red:hover {
    transform: scale(1.5);
}
/* ===============================
 * Blue badge
 * ============================ */
 .badges_blue {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 1px 5px;
    background: #007AFF;
    border-radius: 100px;
    cursor: pointer;
    transition: 100ms;
    margin: 8px;
}
.badges_blue:hover {
    transform: scale(1.5);
}
/* ===============================
 * Badge text
 * ============================ */
.badges__count {
    font-size: 12px;
    font-weight: 500;
    color: #fff;
    user-select: none;
}