.modal-popup {
    position: relative;
    display: inline-grid;
    justify-content: center;
    align-items: center;
    padding: 12px 32px;
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 12px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, .1);
}
.modal-popup__buttons {
    justify-self: center;
}
/* ===============================
 * Animation
 * ============================ */
.modal-popup-enter {
    transform: scale(.9);
    opacity: 0.01;
}
  
.modal-popup-enter.modal-popup-enter-active {
    transform: scale(1);
    opacity: 1;
    transition: opacity 500ms ease-in;
}
  
.modal-popup-leave {
    opacity: 1;
}
  
.modal-popup-leave.modal-popup-leave-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in;
}
.modal-popup-appear {
    transform: scale(.9);
    opacity: 0.01;
}
  
.modal-popup-appear.modal-popup-appear-active {
    transform: scale(1);
    opacity: 1;
    transition: opacity .15s ease-in;
}