.email-input {
    position: relative;
    height: 32px;
    padding: 0 128px 0 8px;
    display: inline-flex;
    align-items: center;
    border: 1px solid #e5e5e5;
    border-radius: 6px;
    cursor: text;
    transition: 100ms;
}
.email-input__title {
    position: relative;
    top: -1px;
    font-size: 14px;
    color: #000;
}
.email-input__title_placeholder {
    position: relative;
    top: -1px;
    font-size: 14px;
    color: rgba(0, 0, 0, .3);
}
.email-input:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, .12);
}