/* ===============================
 * Checkbox container
 * ============================ */
.checkbox {
    position: relative;
    display: grid;
    grid-template-columns: 16px 1fr;
    grid-gap: 10px;
    justify-content: center;
    align-items: center;
    transition: 100ms ease-in;
    cursor: pointer;
    margin: 8px;
}
.checkbox_active {
}
/* ===============================
 * Checkbox handle
 * ============================ */
.checkbox__handle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
    background: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
    transition: 100ms ease-in;
}
.checkbox__handle_active {
    border: 1px solid #007AFF;
    transition: 100ms ease-in;
}
.checkbox__icon {
    position: relative;
    width: 12px;
    height: 10px;
    background: url(../../resources/images/checkbox.svg) no-repeat;
    background-size: contain;
    transition: 100ms ease-in;
    opacity: 0;
}
.checkbox__icon_active {
    position: relative;
    width: 12px;
    height: 10px;
    background: url(../../resources/images/checkbox.svg) no-repeat;
    background-size: contain;
    opacity: 1;
    transition: 100ms ease-in;
}
.checkbox__title {
    display: inline-flex;
    position: relative;
    font-size: 14px;
    user-select: none;
}