.radio-button {
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 1px solid #E5E5E5;
    cursor: pointer;
    border-radius: 100px;
    transition: 100ms ease-in;
    margin: 8px;
}
.radio-button_active {
    border: 1px solid #007AFF;
    transition: 100ms ease-in;
}
.radio-button__handle {
    width: 8px;
    height: 8px;
    background: #fff;
    border-radius: 100px;
    transition: 100ms ease-in;
}
.radio-button__handle_active {
    background: #007AFF;
    border-radius: 100px;
    transition: 100ms ease-out;
}