.menu {
    position: relative;
    top: 40px;
    padding-left: 8px;
}
.menu__links {
    position: relative;
    top: 20px;
}
.menu_opened {
    position: relative;
    width: calc(235px - 8px);
    height: calc(100% - 40px);
    border-right: 1px solid #e5e5e5;
    transition: 150ms;
}
.menu_hidden {
    position: relative;
    width: 40px;
    height: calc(100% - 40px);
    border-right: 1px solid #e5e5e5;
    transition: 150ms;
}
.menu__hide-button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 16px;
    height: 16px;
    background: url(../../resources/images/menu-hide-button.svg) no-repeat;
    background-size: contain;
    cursor: pointer;
}
.menu__show-button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 16px;
    height: 16px;
    background: url(../../resources/images/menu-show-button.svg) no-repeat;
    background-size: contain;
    cursor: pointer;
}