.dropdown {
    width: 223px;
    height: 31px;
    user-select: none;
    margin: 8px;
}
/* ===============================
 * Dropdown Input
 * ============================ */
.dropdown__input_active {
    border: 1px solid #007AFF;
    box-shadow: 0;
}
.dropdown__input {
    width: 223px;
    height: 31px;
    display: grid;
    grid-template-columns: auto 20px;
    align-items: center;
    background: #fff;
    border: 1px solid #e5e5e5;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .04);
    border-radius: 6px;
    transition: 150ms;
    cursor: pointer;
}
.dropdown__input:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, .12);
}
/* ===============================
 * Dropdown list
 * ============================ */
.dropdown__list-items {
    position: relative;
    top: 2px;
    width: 223px;
    height: auto;
    display: grid;
    /* grid-row-gap: 8px; */
    align-items: center;
    background: #fff;
    border: 1px solid #e5e5e5;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .04);
    border-radius: 6px;
}
.dropdown__item {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 32px;
    top: -1px;
    cursor: pointer;
    transition: 75ms;
}
.dropdown__item:hover {
    background: #007AFF;
}
.dropdown__item:first-child {
    margin-top: 8px;
}
.dropdown__item:last-child {
    margin-bottom: 7px;
}
/* ===============================
 * Text Style
 * ============================ */
.dropdown__input-text {
    position: relative;
    top: -1px;
    left: 10px;
    font-size: 14px;
}
/* ===============================
 * Dropdown icon
 * ============================ */
.dropdown__input-icon {
    width: 10px;
    height: 6px;
    background: url(../../resources/images/dropdown-arrow.svg) no-repeat;
    background-size: contain;
    cursor: pointer;
}