.header__devino-logo {
    display: grid;
    grid-template-columns: 24px auto;
    align-items: center;
    cursor: pointer;
}
.devino-logo__sign {
    width: 24px;
    height: 24px;
    background: #BABCBE;
    border-radius: 100px;
}
.devino-logo__text-sign {
    font-size: 14px;
    font-weight: 500;
    margin-left: 8px;
    user-select: none;
}