.close-button {
    width: 16px;
    height: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.close-button__icon {
    width: 8px;
    height: 8px;
    background: url(../../resources/images/cross-icon.svg) no-repeat;
    background-size: contain;
}