body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif; */
}
.App {
  position: absolute;
  width: 100%;
  height: 100%;
  min-width: 1000px !important;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template: 1fr;
  color: #1D1E1F;
}
.content {
  position: relative;
  height: calc(100% - 64px);
  top: 40px;
  padding: 24px 40px;
}
.link {
  display: inline-flex;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}
.link:hover {
  color: #007AFF;
}
.link_regular {

}
/* ======================================
 * Text Styles
 * ==================================== */

.content__regular-text {
  font-size: 15px;
  line-height: 1.5;
}
.content__subheader {
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: -.12px;
  color:  #1D1E1F
}
.content__text {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
  color:  #1D1E1F
}
.content__td-header {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color:  #1D1E1F
}
.content__descriptions {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
  color:  #8E8E8F
}
.content__links {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.2;
  color:  #0198FF
}
.content__menu-item-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color:  #1D1E1F
}
.content__menu-subitem-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color:  #1D1E1F
}
.content__profile-subitem-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color:  #1D1E1F
}
.content__folder-title {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.2;
  letter-spacing: -.18px;
  color:  #1D1E1F
}