.App {
  position: absolute;
  width: 100%;
  height: 100%;
  min-width: 1000px !important;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template: 1fr;
  color: #1D1E1F;
}
.content {
  position: relative;
