.switcher {
    position: relative;
    display: flex;
    align-items: center;
    width: 32px;
    height: 16px;
    background: #e5e5e5;
    border-radius: 100px;
    cursor: pointer;
    transition: 150ms ease-in;
    margin: 8px;
}
.switcher_active {
    background: #007AFF;
    transition: 150ms ease-out;
}
.switcher__handle {
    position: relative;
    transform: translateX(1px);
    width: 14px;
    height: 14px;
    background: #fff;
    border-radius: 100px;
    transition: 100ms ease-in;
}
.switcher__handle_active {
    transform: translateX(17px);
    background: #fff;
    transition: 100ms ease-out;
}