.header__notification-button {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    justify-self: end;
}
.notification-icon {
    width: 16px;
    height: 20px;
    background: url(../../resources/images/notification-icon.svg) no-repeat;
    background-size: contain;
}
.notification-icon:hover {
    width: 16px;
    height: 20px;
    background: url(../../resources/images/notification-icon-hover.svg) no-repeat;
    background-size: contain;
}