.header__profile-button {
    display: grid;
    grid-template-columns: auto 24px;
    align-items: center;
    cursor: pointer;
}
.profile-button__username {
    font-size: 15px;
    font-weight: 500;
    margin-right: 8px;
}
.profile-button__username:hover {
    color: red;
}
.profile-button__avatar {
    width: 24px;
    height: 24px;
    background-size: contain;
    background: #ccc;
    border-radius: 100px;
    overflow: hidden;
}