
/* ===================================
 * Menu Item
 * ================================= */
 .menu__item {
    position: relative;
    width: calc(130px - 5px);
    height: 32px;
    display: grid;
    padding-left: 5px;
    grid-template-columns: 24px 1fr;
    align-items: center;
    background: #fff;
    border-radius: 6px;
    cursor: pointer;
    transition: 150ms;
    margin-bottom: 4px;
}
.menu__item_active {
    position: relative;
    width: calc(130px - 5px);
    height: 32px;
    display: grid;
    padding-left: 5px;
    grid-template-columns: 24px 1fr;
    align-items: center;
    background: #007AFF;
    border-radius: 6px;
    cursor: pointer;
    transition: 150ms;
    margin-bottom: 4px;
}
.menu__icon {
    position: relative;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.menu__item-title {
    position: relative;
    top: -1px;
    font-size: 14px;
    margin-left: 5px;
    color: #000;
    transition: 150ms;
}
.menu__item-title_active {
    color: #fff;
}
