.profile-button__avatar {
    cursor: pointer;
    width: 24px;
    height: 24px;
    margin: 8px;
}
.profile-button__avatar_s {
    width: 24px;
    height: 24px;
}
.profile-button__avatar_m {
    width: 32px;
    height: 32px;
}
.profile-button__avatar_xl {
    width: 40px;
    height: 40px;
}
/* ===============================
 * Avatar placeholder
 * ============================ */
.profile-button__placeholder {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    background: #e5e5e5;
    border-radius: 100px;
    margin: 8px;
    overflow: hidden;
}
.profile-button__placeholder_s {
    width: 24px;
    height: 24px;
    background: #e5e5e5;
    border-radius: 100px;
}
.profile-button__placeholder_m {
    width: 32px;
    height: 32px;
    background: #e5e5e5;
    border-radius: 100px;
}
.profile-button__placeholder_xl {
    width: 40px;
    height: 40px;
    background: #e5e5e5;
    border-radius: 100px;
}
.avatar-placeholder__icon {
    width: 12px;
    height: 12px;
    background: url(../../resources/images/avatar-placeholder.svg) no-repeat;
    background-size: contain;
}
.avatar-placeholder__icon_s {
    width: 12px;
    height: 12px;
    background: url(../../resources/images/avatar-placeholder.svg) no-repeat;
    background-size: contain;
}
.avatar-placeholder__icon_m {
    width: 18px;
    height: 18px;
    background: url(../../resources/images/avatar-placeholder.svg) no-repeat;
    background-size: contain;
}
.avatar-placeholder__icon_xl {
    width: 24px;
    height: 24px;
    background: url(../../resources/images/avatar-placeholder.svg) no-repeat;
    background-size: contain;
}