:root {
    --regular-font: 13px;
    --regular-font-weight: 400;
    --regular-radius: 6px;
}
.action-button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    padding: 0px 16px;
    margin: 8px;
}
.action-button__title {
    position: relative;
    top: -1px;
    font-size: var(--regular-font);
    font-weight: var(--regular-font-weight);
    line-height: 1;
}
/* ===================================
 * Solid Button
 * ================================= */
.action-button_blue-solid {
    background: #007AFF;
    border-radius: var(--regular-radius);
    cursor: pointer;
    transition: .75ms;
}
.action-button_blue-solid:hover {
    background: #0198FF;
}
.action-button_blue-solid:active {
    background: #0054B9;
}
.action-button_red-solid {
    background: #FF4E00;
    border-radius: var(--regular-radius);
    cursor: pointer;
    transition: .75ms;
}
.action-button_red-solid:hover {
    background: #FF6251;
}
.action-button_red-solid:active {
    background: #DF3900;
}
.action-button__title {
    font-size: var(--regular-font);
    font-weight: var(--regular-font-weight);
    line-height: 1;
    user-select: none;
}
.action-button__title_blue-solid {
    font-size: var(--regular-font);
    font-weight: var(--regular-font-weight);
    line-height: 1;
    color: #fff;
    user-select: none;
}
.action-button__title_red-solid {
    font-size: var(--regular-font);
    font-weight: var(--regular-font-weight);
    line-height: 1;
    color: #fff;
    user-select: none;
}
/* ===================================
 * Stroke Button
 * ================================= */
.action-button_blue-stroke {
    background: #fff;
    border: 1px solid #E5E5E5;
    border-radius: var(--regular-radius);
    cursor: pointer;
    transition: .75ms;
}
.action-button_blue-stroke:hover {
    border: 1px solid #B5B5B5;
}
.action-button_red-stroke {
    background: #fff;
    border: 1px solid #FF4E00;
    border-radius: var(--regular-radius);
    cursor: pointer;
    transition: .75ms;
}
.action-button_red-stroke:hover {
    border: 1px solid #FF6251;
}
.action-button_red-stroke:active {
    border: 1px solid #DF3900;
}
.action-button__title_blue-stroke {
    color: #000;
    user-select: none;
}
.action-button__title_red-stroke {
    color: #FF4E00;
    user-select: none;
}
/* ===============================
 * Shadow Button
 * ============================ */
.action-button_shadow {
    background: #fff;
    border-radius: var(--regular-radius);
    cursor: pointer;
    transition: 150ms;
    border: 1px solid #e5e5e5;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .04);
}
.action-button_shadow:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, .12);
}
.action-button_shadow:active {
    background: #e5e5e5;
}
/* ===============================
 * Disabled Button
 * ============================ */
.action-button_disabled {
    background: #E5E5E5;
    border-radius: var(--regular-radius);
    transition: 150ms;
}
.action-button_disabled {
    background: #E5E5E5;
    border-radius: var(--regular-radius);
    transition: 150ms;
}
.action-button__title_disabled {
    font-size: var(--regular-font);
    font-weight: var(--regular-font-weight);
    line-height: 1;
    color: #8E8E8F !important;
    user-select: none;
}