/* ===============================
 * Sidebar Item
 * ============================ */
.style-guide__sidebar__item {
    padding-left: 15px;
    display: grid;
    align-items: center;
    height: 24px;
    font-size: 14px;
    cursor: pointer;
    color: #8E8E8F;
    user-select: none;
}
.style-guide__sidebar__item_active {
    padding-left: 15px;
    display: grid;
    align-items: center;
    height: 24px;
    font-size: 14px;
    color: #1D1E1F;
    user-select: none;
}
.style-guide__sidebar__item:hover {
    color: red;
}
.style-guide__sidebar__item:last-child {
    margin-bottom: 40px;
}
.style-guide__sidebar__item_active:last-child {
    margin-bottom: 40px;
}