/* ===============================
 * Sidebar
 * ============================ */
.style-guide__sidebar {
    position: fixed;
    overflow: scroll;
    top: 0;
    left: 0;
    width: 264px;
    height: calc(100% - 7px);
    padding-top: 7px;
    background: #fff;
    border-right: 1px solid #e5e5e5;
    display: grid;
    grid-template-columns: 105px 1fr;
    z-index: 9999;
}
/* ===============================
 * Content Area
 * ============================ */
.style-guide-content {
    position: absolute;
    left: 200px;
    width: calc(100% - 336px);
    height: calc(100% - 80px);
    padding: 40px;
    padding-left: 96px;
}
.style-guide-content__item-content {
    position: relative;
    width: 100%;
    height: 100%;
}
.style-guide-content__h1 {
    width: 70%;
    font-size: 36px;
    line-height: 1.2;
    font-weight: 600;
    margin-bottom: 15px;
}
.style-guide-content__h2 {
    width: 70%;
    font-size: 26px;
    line-height: 1.2;
    font-weight: 500;
    margin-bottom: 15px;
}
.style-guide-content__h3 {
    width: 70%;
    font-size: 22px;
    line-height: 1.2;
    font-weight: 500;
    margin-bottom: 15px;
}
.style-guide-content__lead {
    width: 70%;
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 20px;
}
.style-guide-content__text {
    width: 70%;
    font-size: 18px;
    line-height: 1.5;
    margin-bottom: 20px;
}
.style-guide__typo-variations {
    display: grid;
    grid-template-columns: 200px 50%;
    line-height: 1.5;
    grid-gap: 50px;
    align-items: end;
}
.style-guide__description {
    padding-top: 40px;
    padding-bottom: 40px;
}
.style-guide-content__code-widget {
    display: inline-flex;
    padding: 12px;
    font-family: "Courier";
    background: #F7F6F3;
    border-radius: 6px;
    margin-top: 12px;
    margin-bottom: 12px;
}
/* ===============================
 * Color Template
 * ============================ */
.style-guide__color-temlate-container {
    display: grid;
    grid-gap: 8px;
}
.color-temlate-container__color-template {
    position: relative;
    width: 88px;
    height: 32px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    font-size: 13px;
    letter-spacing: .13px;
    font-weight: 500;
}
/* ===============================
 * UI Presentation frame
 * ============================ */
.style-guide-content__ui-presenation-frame {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% - 80px);
    border: 1px solid #e5e5e5;
    padding: 40px;
    margin-top: 40px;
    margin-bottom: 40px;
    border-radius: 9px;
    overflow: hidden;
}
.style-guide-content__ui-presenation-frame_dark {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: calc(100% - 80px);
    background: rgba(0, 0, 0, .08);
    padding: 40px;
    margin-top: 40px;
    margin-bottom: 40px;
    border-radius: 9px;
    overflow: hidden;
}