/* ===============================
 * Button plate
 * ============================ */
.attach-button {
    position: relative;
    display: inline-flex;
    align-items: center;
    height: 30px;
    padding: 0px 16px 0px 8px;
    background: #fff;
    border: 1px solid rgba(0, 0, 0, .12);
    box-shadow: 0 2px 3px rgba(0, 0, 0, .04);
    border-radius: 7px;
    cursor: pointer;
    margin: 8px;
    transition: 100ms;
    user-select: none;
}
.attach-button_disabled {
    border: 1px solid #e5e5e5;
    background: #e5e5e5;
    box-shadow: none !important;
    cursor: default;
}
.attach-button_disabled:hover {
    background: #e5e5e5;
}
.attach-button:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, .12);
}
.attach-button:active {
    box-shadow: none;
}
/* ===============================
 * Icon
 * ============================ */
.attach-button__icon {
    width: 18px;
    height: 18px;
    margin-right: 6px;
    background: url(../../resources/images/attach-icon.svg) no-repeat;
    background-size: contain;
}
.attach-button__icon_disabled {
    background: url(../../resources/images/attach-icon-disabled.svg) no-repeat;
    background-size: contain;
}
/* ===============================
 * Title
 * ============================ */
.attach-button__title {
    position: relative;
    top: -1px;
    font-size: 13px;
    line-height: 1;
}
.attach-button__title_disabled {
    color: #fff;
}
/* ===============================
 * File type text
 * ============================ */
.attach-button__file-type {
    position: absolute;
    top: 40px;
    font-size: 13px;
    opacity: .6;
}
.attach-button__file-type_disabled {
    color: #e5e5e5;
    opacity: 1 !important;
}